:root{
    --primary-color: #e0c521;
}
.card__container{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 300px;

}
.card{
    /* display: flex; */
    /* flex-direction: column; */
    /* justify-content: space-between; */
    width: 140px;
    border: 1px solid #b4aaa100;
    padding: .0rem .0rem;
    margin: 20px 5px;
    position: relative;
    border-radius: 10px;
}

.card__title{
    font-size: 15px;
    font-weight: 400;
    text-align: center;
    margin-top: 3px;
    margin-bottom: 10px;
    line-height: 20px;
   
    
}

.card__price{
    /* margin-top: auto; */
    font-size: 18px;
    color: rgb(6, 247, 18);
    /* font-weight: bold; */
}
.image__container{
    width: 100%;
    min-height: 150px;
    margin: 0 auto;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 0px 0px 20px 0px rgba(185, 185, 185, 0.152);
    
}
.image__container img{
    height: 100%;
    width: 100%;
    object-fit: cover;
    

}


.btn__container{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-top: 10px;

}

.card__price{
    text-align: center;
    /* display: inline-block; */
}

.card__badge{
    position: absolute;
    top: 12px;
    right: 13px;
    width: 16px;
    height: 16px;
    /* background-color: rgb(227, 112, 30); */
    border-radius: 50px;
    transform: translate(20px, -20px) scale(2);
    color: white;
    font-weight: bold;
    text-align: center;
    border: 2px solid  rgba(255, 255, 255, 0.615);
    font-size: 11px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .3s ease;
    animation: scaleamnimation 1s ease infinite;
    opacity: 0.8;
}
@keyframes scaleamnimation {
    to{
        transform: translate(20px, -20px) scale(1.05);
        opacity: 1;

    }
}
.card__badge--hidden{
    display: none;
}