:root{
    --primary-color: #e0c521;
}

.btn{
    padding: .2rem .4rem;
    font-size: 1.2rem;
    text-align: center;
    border: none;
    outline: none;
    border-radius: 10px;
    /* margin-left: 10px; */
    box-shadow: 1px -3px 52px -5px rgba(0, 0, 0, 0.77);
    cursor: pointer;
    width: 130px;
    display: block;

}

.btn.add{
    background-color: var(--primary-color);
    margin: 2px;
    
}
.btn.remove{
    
    background-color: tomato;
    margin: 2px;
}

.btn.checkout{
    background-color: rgb(67, 159, 67);
    width: 150px;
    color: white;
    font-size: 14px;
    padding: 10px;
}
.btn:active{
    transform: scale(.98);
}

.btn.add:hover{
    background-color: #cab320;
}
.btn.add:active{
    border-color: #ad9a1c;
}
.btn.remove:hover{
    border-color: rgb(209, 83, 61);
}
.btn.remove:active{
    border-color: rgb(185, 73, 54);
}
.btn.checkout:hover{
    border-color: rgb(41, 141, 41);
}
.btn.checkout:active{
    border-color: rgb(30, 91, 30);
}
